export default {
	name: 'UniNumberBox',
	props: {
		value: {
			type: [Number, String],
			default: 0
		},
		min: {
			type: Number,
			default: 0
		},
		max: {
			type: Number,
			default: 100
		},
		step: {
			type: Number,
			default: 1
		},
		disabled: {
			type: Boolean,
			default: false
		},
		index:{
			type: [Number, String],
		}
	},
	data() {
		return {
			inputValue: 0,
			windowHeight: '',
			modelValue:0,//model
			showHide:false,//显示隐藏
		}
	},
	watch: {
		value(val) {
			this.inputValue = +val
		},
		inputValue(newVal, oldVal) {
			if (+newVal !== +oldVal) {
				let a=null
				if(this.index!=undefined){
					a=[this.index,newVal]
				}else{
					a=newVal
				}
				this.$emit('change', a)
			}
		}
	},
	created() {
		this.inputValue = +this.value
	},
	methods: {
		_calcValue(type) {
			if (this.disabled) {
				return
			}
			const scale = this._getDecimalScale()
			let value = this.inputValue * scale
			let step = this.step * scale
			if (type === 'minus') {
				value -= step
			} else if (type === 'plus') {
				value += step
			}
			if (value < this.min || value > this.max) {
				return
			}
			this.inputValue = value / scale
		},
		_getDecimalScale() {
			let scale = 1
			// 浮点型
			if (~~this.step !== this.step) {
				scale = Math.pow(10, (this.step + '').split('.')[1].length)
			}
			return scale
		},
		_onBlur(event) {
			let value = event.detail.value
			console.log(value)
			return;
			if (!value) {
				this.inputValue = 0
				return
			}
			value = +value
			if (value > this.max) {
				value = this.max
			} else if (value < this.min) {
				value = this.min
			}
			this.inputValue = value
		},
		// 显示model
		ifShow(e){
			this.modelValue=e
			console.log(this.modelValue)
			this.showHide=true
		},
		//隐藏model
		modelHide(){
			this.showHide=false
		},
		// 确定
		confirm(){
			if(this.modelValue>this.max){
				this.inputValue=this.max
			}else{
				this.inputValue=this.modelValue
			}
			this.showHide=false
		}
	}
}
